<template>
  <div class="seller" ref="seller">
    <div class="seller-content">
      <div class="overview">
        <div class="title">{{seller.name}}</div>
        <div class="desc border-1px">
          <Star :size="36" :score="seller.score"></Star>
          <div class="text">{{seller.ratingCount}}</div>
          <div class="text">月售{{seller.sellCount}}单</div>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
            <span class="stress">{{seller.minPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
            <span class="stress">{{seller.deliveryPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <div class="content">
            <span class="stress">{{seller.deliveryTime}}</span>分钟
            </div>
          </li>
        </ul>
        <div class="favorite" @click="toggleFavorite">
          <span class="icon-favorite" :class="{ 'active': favorite }"></span>
          <span class="text">{{favoriteText}}</span>
        </div>
      </div>
      <Split></Split>
      <div class="bulletin">
        <div class="title">公告与活动</div>
      </div>
      <div class="content-wrapper border-1px">
        <p class="content">{{seller.bulletin}}</p>
      </div>
      <ul v-if="seller.supports" class="supports">
        <li class="support-item border-1px" v-for="(item,index) in seller.supports" :key="index">
          <Activitiesicon :size="4" :type="item.type"></Activitiesicon>
          <span class="text" >{{item.description}}</span>
        </li>
      </ul>
      <Split></Split>
      <div class="pics">
        <div class="title">商家实景</div>
        <div class="pic-wrapper"  ref="picWrapper">
          <ul class="pic-list" ref="picList">
            <li class="pic-item" v-for="(pic, index) in seller.pics" :key='index'>
              <img :src="pic" width="120" height="90" alt="">
            </li>
          </ul>
        </div>
      </div>
      <Split></Split>
      <div class="info">
        <h1 class="title border-1px">商家信息</h1>
        <ul>
          <li class="info-item" v-for="(info, index) in seller.infos" :key="index">
            {{info}}
          </li>
        </ul>
      </div>
    </div>
  </div>  
</template>

<script>
  import Star from '../../components/star/Star.vue'
  import Split from '../../components/split/Split.vue'
  import Activitiesicon from '../../components/activitiesIcon/Activitiesicon'
  import BScroll from 'better-scroll'
  import {saveToLocal,loadFromLocal} from '../../assets/js/store'
  export default {
    components: {
      Star,
      Split,
      Activitiesicon
    },

    props: {
      seller: {
        type: Object
      }
    },

    data () {
      return {
        classMap: ['decrease', 'discount', 'special', 'invoice', 'guarantee'],
        favorite: (()=>{
          return loadFromLocal(1, 'favorite', false);
        })(),
      };
    },

    computed: {
      favoriteText(){
        return this.favorite? '已收藏' : '未收藏';
      }
    },

    watch: {
      seller(){
        if (this.seller.pics){
          let picWidth = 120;
          let margin = 6 
          let width = (picWidth + margin) * this.seller.pics.length - margin;
          this.$refs.picList.style.width = width + "px";
          this.$nextTick(()=>{
            this.picScroll = new BScroll(this.$refs.picWrapper, {
              scrollX: true,
              eventPassthrough: 'vertical'
            })
          })
        }
      }
    },

    mounted(){
      this.scroll = new BScroll(this.$refs.seller,{
        click: true
      })
    },

    methods: {
      toggleFavorite(){
        this.favorite = !this.favorite
        saveToLocal(1, 'favorite', this.favorite)
      }
    },

}
</script>
