<template>
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title">综合评分</div>
          <div class="rank">高于周边商家{{seller.rankRate}}%</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <div class="title">服务态度</div>
            <Star :size='36' :score="seller.serviceScore"></Star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
              <div class="title">商品评分</div>
              <Star :size='36' :score="seller.foodScore"></Star>
              <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrapper">
            <span class="title">送达时间</span>
            <span class="delivery">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <Split></Split>
      <RatingSelected :select-type="selectType" :only-content="onlyContent"  :ratings="ratings" @only-content="toggleContent" @selected="selected"></RatingSelected>
      <div class="rating-wrapper">
        <ul>
          <li class="rating-item border-1px" v-for="(rating, index) in ratings" :key="index" v-show="needShow(rating.rateType,rating.text)">
            <div class="avatar">
              <img :src="rating.avatar" width="28" height="28" alt="">
            </div>
            <div class="content">
              <h1 class="name">{{rating.username}}</h1>
              <div class="star-wrapper">
                <Star :size='24' :score="rating.score"></Star>
                <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-if="rating.recommend && rating.recommend.length">
                <span class="icon-thumb_up"></span>
                <span class="item" v-for="(item, index) in rating.recommend" :key="index">{{item}}</span>
              </div>
              <div class="un-recommend" v-if="!rating.recommend || rating.rateType === 1">
                <span class="icon-thumb_down"></span>
              </div>
              <div class="time">
                {{rating.rateTime | formatDate}}
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import Star from '../../components/star/Star.vue'
  import Split from '../../components/split/Split.vue'
  import RatingSelected from '../../components/ratingSelected/RatingSelected.vue'
  import {formatDate} from '../../assets/js/date'
  import BScroll from 'better-scroll'
  const ALL = 2;
  const ERR_OK = 0;
  export default {
    components: {
      Star,
      Split,
      RatingSelected
    },

    props: ["seller"],

    data() {
      return {
        ratings:[],
        selectType: ALL,
        onlyContent: true,
        scroll
      };
    },
    computed: {},

    created(){
      this.$axios.get('/api/ratings').then( response => {
        let result = response.data
        if (result.errno == ERR_OK ){
          this.ratings = result.data
          this.scroll = new BScroll(this.$refs.ratings,{
            click: true
          });
        };
      })
    },

    methods: {
      toggleContent() {
        this.onlyContent = !this.onlyContent
      },
      selected(type) {
        this.selectType = type;
      },
      needShow(type, text) {
        if (this.onlyContent && !text){
          return false;
        }
        if (this.selectType == ALL){
          return true;
        } else {
          return type === this.selectType;
        }
      }
    },

    filters: {
      formatDate(time) {
        let date = new Date(time);
        return formatDate(date,'yyyy-MM-dd hh:mm');
      }
    }
  };
</script>
