<template>
  <div class="star" :class="startType">
    <span v-for="(item,index) in itemClasses" :key="index" :class="item" class="star-item"></span>
  </div>
</template>

<script>
  const LENGTH = 5;
  const CLS_ON = 'on';
  const CLS_HALF = 'half';
  const CLS_OFF = 'off';

  export default {
    components: {},
    props: [
      'size',
      'score'
    ],
    data() {
      return {};
    },

    computed: {
      startType() {
        return "star-" + this.size;
      },
      itemClasses() {
        let result = [];        
        let score = Math.floor(parseFloat(this.score) * 2) / 2;
        let hasDecimal = score % 1 !== 0;
        let integer = Math.floor(score)
        for (let i = 0; i < integer; i++){
          result.push(CLS_ON);
        }
        if (hasDecimal){
          result.push(CLS_HALF)
        }
        while (result.length < LENGTH){
          result.push(CLS_OFF)
        }
        return result;
      }
    },

    methods: {}
  };
</script>
